/**
 * Basic typography style for copy text
 */
body {
  color: $text-color;
  font: normal 100% / 1.4 $text-font-stack;
}

h1, h2, h3, h4, h5, h6 {
  font-family: $heading-font-stack;
  font-weight: normal;
  text-transform: uppercase;
  line-height: 1em;
  margin: 0;
}


.text-align--left {
  @include text-align(left);
}

.text-align--right {
  @include text-align(right);
}

.text-align--center {
  @include text-align(center);
}